
.update-one{
  opacity: 0.01;
  position: absolute;
  right: 0;
  top: 0;
}
.year{
  margin: 0 1.2rem;
}
.diary-list-item{
  margin: 0.5rem 0;
}
.container {
  padding: 5px !important;
}
.list-group li {
  margin-top: 0.5rem;
}
.avatar {
  background: url("http://qiniu-zizyblog.shushubuyue.net/avatar.jpg");
  width: 100px;
  height: 100px;
  background-size: cover;
  margin: auto;
  border-radius: 10px;
}
.loading{
  text-align: center;
  margin: 4rem;
  font-size: 1.5rem
}
.header {
  text-align: center;
}
.content{
  margin: 0 0.5rem;
}
.content .list-group-item {
  padding: .55rem 0.55rem;
  height: 100px;
}
.content .list-group-item-expand {
  height: auto !important;
}
.content .img {
  float: left;
  margin: 0rem 0.7rem 0rem 0rem;
  border-radius: 5px;
  background-size: cover;
  width: 80px;
  overflow: hidden;
  height: 80px;
}
.content .img-expand {
  float: none;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  max-width: 350px;
  margin: auto;
}
.content .title {
  font-weight: 900;
  margin-right: 5rem;
  white-space: nowrap;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;
}
.content .title-expand {
  white-space: normal !important;
}
.content .text {
  margin-right: 5rem;
  height: 3.2rem;
  white-space: normal;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;
}
.text-expand {
  overflow: hidden;
  height: auto !important;
}
.content .month {
  position: absolute;
  right: 2.5rem;
  font-size: 12pt;
  display: flex !important;
  top: -2rem;
  height: 100%;
  align-items: center;
}
.content .day {
  position: absolute;
  right: 1rem;
  font-size: 30pt;
  display: flex !important;
  top: 0;
  height: 100%;
  align-items: center;
}




